<template>
    <div style="overflow: hidden;">
        <div class="a-in-stop-head">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/parkMapAdmin/index', query: { manage: 'eatList' } }"></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div style="margin:0 30px; ">
            <el-form :model="addEatReq" :rules="addEatRules" ref="addEatReq" label-width="130px" class="demo-ruleForm">
                <div  class="a-in-stop-title"></div>
                <div class="a-in-stop-box">
                    <el-form-item label="" prop="name">
                        <el-input v-model="addEatReq.name" style="width: 251px!important;"></el-input>
                    </el-form-item>
                    <el-form-item label="LOGO" prop="logoUrl">
                        <gt-material prop="logoUrl" :url="addEatReq.logoUrl" v-on:getChangeUrl="getChangeUrl" width="80" height="80"></gt-material>
                        <p class="a-in-stop-prompt">1:1</p>
                    </el-form-item>
                    <el-form-item label="" prop="introduce">
                        <el-input v-model="addEatReq.introduce" style="width: 251px!important;"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="address">
                        <gtmap :gtmapInformation.sync="mapBean" v-on:getMapData="getMapData" style="width: 251px!important;" ></gtmap>
                    </el-form-item>
                    <el-form-item label="" prop="phone">
                        <el-input v-model="addEatReq.phone" style="width: 251px!important;"></el-input>
                    </el-form-item>
                </div>
                <div class="a-in-stop-title"></div>
                <div class="a-in-stop-box">
                    <el-form-item label="Banner" prop="bannerUrl">
                        <gt-material prop="bannerUrl" :url="addEatReq.bannerUrl" v-on:getChangeUrl="getChangeUrl" width="80" height="80"></gt-material>
                        <p class="a-in-stop-prompt">360*200</p>
                    </el-form-item>
                    <el-form-item label="" prop="remark">
                        <el-input type="textarea" v-model="addEatReq.remark" style="width: 300px!important;"></el-input>
                    </el-form-item>
                    <el-form-item style="margin-top: 80px;">
                        <el-button type="primary" @click="submitForm('addEatReq')"></el-button>
                        <el-button type="primary" @click="resetForm('addEatReq')"></el-button>
                    </el-form-item>
                </div>
            </el-form>
        </div>
    </div>
</template>
<script>
import { requestAddEat } from '../api/api';
import gtmap from '@/components/PublicVue/map/gtMap.vue';
export default {
  data() {
    return {
      addEatReq: {
        name: '', // 
        logoUrl: '', // logo
        introduce: '', // 
        address: '', // 
        phone: '', // 
        bannerUrl: '', // banner
        remark: '', // 
        lat: '', // 
        lon: '' // 
      },
      mapBean: {
        label: '',
        detailedAddress: ''
      },
      addEatRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 7, message: '7', trigger: 'blur' }
        ],
        logoUrl: [{ required: true, message: 'logo', trigger: 'blur' }],
        introduce: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 7, message: '7', trigger: 'blur' }
        ],
        address: [{ required: true, message: '', trigger: 'blur' }],
        phone: [{ required: true, message: '', trigger: 'blur' }],
        // lat: [{ required: true, message: "", trigger: "blur" }],
        bannerUrl: [
          { required: true, message: 'banner', trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    //
    getMapData(e){
      console.log(e,'mapdata')
      this.addEatReq.address = e.name
      this.addEatReq.lat = e.lat
      this.addEatReq.lon = e.lng
      
    },
    submitForm(formName) {
      console.log(this.addEatReq);
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.addEat();
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.$router.push({
        path: '/parkMapAdmin/index',
        query: { manage: 'eatList' }
      });
    },
    getChangeUrl(e) {
      // url
      this.addEatReq[e.prop] = e.url;
    },
    addEat() {
      requestAddEat(this.addEatReq).then(data => {
        // console.log(data);
        var _code = data.code;
        if (_code == 100) {
          this.$message({
            message: '',
            type: 'success'
          });
          this.$router.push({
            path: '/parkMapAdmin/index',
            query: { manage: 'eatList' }
          });
        } else {
          this.$message.error(data.msg + '[' + _code + ']');
        }
      });
    }
  },
  components: {
    gtmap
  },
  watch: {
    mapBean: function(val) {
      //
      console.log('watch', val);
      this.addEatReq.address = val.MapData.address;
      this.addEatReq.lat = val.MapData.latLng.lat;
      this.addEatReq.lon = val.MapData.latLng.lng;
      console.log(this.addEatReq);
    }
  }
};
</script>
<style>
.a-in-stop-head {
  display: inline-block;
  padding: 30px 0 0 29px;
  font-size: 16px;
  width: 100%;
  height: 51px;
  background-color: rgba(248, 248, 248, 1);
}
.a-in-stop-title {
  width: 749px;
  height: 60px;
  font-size: 14px;
  line-height: 60px;
  margin: 2px 0 35px;
  color: #666;
  border-bottom: 1px solid #ddd;
}
.a-in-stop-box {
  width: 600px;
  padding-left: 80px;
}
.a-in-stop-prompt {
  display: inline-block;
  position: absolute;
  top: 25px;
  left: 100px;
  color: #999;
  font-size: 12px;
}
.el-form-item {
  margin-bottom: 26px;
}
.el-textarea__inner {
  height: 150px;
}
</style>



